
body {
	background: #f6f6f6;
}

.dgds_card {
	background: #fff;
    border: 1px solid #ddd;
	box-shadow: 0 1px 2px rgba(0,0,0,0.025);
	margin-bottom: 15px;
	padding: 15px;
}

.dgds_section-heading {
	border-bottom: 1px solid #e0e0e0;
	text-transform: uppercase;
	font-size: 16px;
	padding: 5px;
}
.bc_link-list {
  margin: 0; }

body {
  font-family: "Crimson Text", serif;
  font-size: 20px;
  font-weight: 400; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif; }

h1 {
  font-weight: 700; }

* {
  box-sizing: border-box; }

.dg_nav {
  margin: 0; }

